          <h3>Purpose</h3>
          <p>
            This app is designed to help settler Australians remember the first names of Country that they might have forgotten or tried to erase.
          </p>
          <p>
            This app is <i>not</i> an authority on First Nations knowledges.
          </p>
          <p>
            This app provides numerous spellings for first names of Country. The purpose of this is two-fold. Firstly, the provision of linguistically derived respellings of colonial records is intended to help settler Australians speak these names as First Nations might have spoken these names in the past, or as First Nations still speak these names today. Secondly, the provision of multiple spellings is intended to reinforce the history of this place: settler Australians' disinterest and destruction of First Nations lives, languages, and cultures, has left in the historical record large gaps that may never be filled, and uncertainties that may never be resolved.
          </p>
          <p>
            If settler Austrlaians find themselves ignorant of a placename close to home, unsure of which respelling to choose, or how to pronounce it, it might prompt them to ask questions about this place's history, and take action to right past wrongs.
